import React from "react";
import levels from "../../progress/module/data/modules"; // Ruta corregida
import NivelCard from "../components/NivelCard";

const ProgressPage = () => {
  console.log("Niveles cargados:", levels); // üîç Verifica si los datos est√°n llegando

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold text-primary-900">Mi Progreso</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {levels.length > 0 ? (
          levels.map((level) => <NivelCard key={level.id} level={level} />)
        ) : (
          <p className="text-red-500">No se encontraron niveles.</p>
        )}
      </div>
    </div>
  );
};

export default ProgressPage;
